body {
  margin: 0;
  padding: 0;
  font-family: 'Future',-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: black;
}

.display-1 {
  color:white;
  font-weight: 500;
}

.nav-scroller {
  margin-top: 2rem;
}

h1 {
  margin-top: 2rem;
}

h3 {
  color: #cccccc;
  margin-top: 2.5rem;
}

h5 {
  margin-top: 1rem;
  color: #cccccc;
}

a {
  color: white;
}

#landing-page-img {
  opacity: 0.35;
  height: 45rem;
  width: 100%;
}

.card img {
  width: 100%;
  height: 15vw;
  object-fit: cover;
}

.about-text {
  color:white;
  margin-top:2rem;
}

label {
  color: white;
}

.card {
  margin-top:2rem;
}

.reveal-articles {
  background-color: black;
  border-color: black;
}

#arrow-down:hover {
  -webkit-filter: drop-shadow(1px 1px 0 lightblue)
                  drop-shadow(-1px -1px 0 lightblue);
  filter: drop-shadow(1px 1px 0 lightblue) 
          drop-shadow(-1px -1px 0 lightblue);
}

.article-desc {
  color: white;
}

.logo {
  margin-top: 2.5rem;
  background-image:url('https://upload.wikimedia.org/wikipedia/commons/thumb/7/70/Solid_white.svg/2000px-Solid_white.svg.png');
  background-color:white;
}

.logo-img {
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding-right: 1rem;
  padding-left : 1rem;
  opacity: .8;
}

#arrow-down {
  width: 5rem;
}

@media (max-width: 900px) {
  .display-1 {
    font-weight: 500;
    font-size: 4rem;
  }
  h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 600px) {
  h3 {
    font-size: 1rem;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

